export function GoogleDorksGenerator() {
  const [domain, setDomain] = useState("")
  const [copiedIndex, setCopiedIndex] = useState<string | null>(null)

  const replaceDomain = (dork: string, inputDomain: string) => {
    if (!inputDomain) return dork
    
    // Split domains by comma and clean up spaces
    const domains = inputDomain.split(',').map(d => d.trim()).filter(d => d.length > 0)
    
    if (domains.length === 1) {
      // Single domain - replace normally
      return dork.replace(/{domain}/g, domains[0])
    } else {
      // Multiple domains - create domain1 | domain2 format (without site: prefix)
      const domainList = domains.join(' | ')
      
      // Check if the dork already has site: prefix
      if (dork.includes('site:{domain}')) {
        // Replace site:{domain} with site:domain1 | site:domain2
        const siteQueries = domains.map(domain => `site:${domain}`).join(' | ')
        return dork.replace(/site:\{domain\}/g, siteQueries)
      } else {
        // Replace {domain} with just the domain list
        return dork.replace(/{domain}/g, domainList)
      }
    }
  }

  const copyToClipboard = async (text: string, index: string) => {
    await navigator.clipboard.writeText(text)
    setCopiedIndex(index)
    setTimeout(() => setCopiedIndex(null), 2000)
  }

  const generateGoogleSearchUrl = (query: string) => {
    return `https://www.google.com/search?q=${encodeURIComponent(query)}`
  }

  const openAllQueries = (dorks: string[]) => {
    // Open each dork individually in separate tabs
    dorks.forEach((dork) => {
      const processedDork = replaceDomain(dork, domain)
      const url = generateGoogleSearchUrl(processedDork)
      window.open(url, "_blank")
    })
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-background via-background to-primary/5 py-16 px-4">
      <div className="max-w-6xl mx-auto">
        <div className="text-center mb-16">
          <div className="inline-flex items-center gap-2 px-4 py-2 bg-primary/10 border border-primary/20 rounded-full mb-6">
            <Sparkles className="h-4 w-4 text-primary" />
            <span className="text-sm font-medium text-primary">Security Research Tool</span>
          </div>

          <h1 className="text-5xl md:text-6xl font-bold bg-gradient-to-r from-primary via-accent to-primary bg-clip-text text-transparent mb-4 text-balance">
            Google Dorks Explorer
          </h1>

          <p className="text-lg text-muted-foreground mb-8 max-w-2xl mx-auto text-pretty">
            Discover vulnerabilities and exposed data with advanced search queries
          </p>

          <div className="text-sm text-muted-foreground mb-6">
            <span>Made by </span>
            <a 
              href="https://github.com/xFraylin" 
              target="_blank" 
              rel="noopener noreferrer"
              className="text-primary hover:text-primary/80 font-medium transition-colors"
            >
              xFraylin
            </a>
            <span> â€¢ Credits to </span>
            <a 
              href="https://www.exploit-db.com/" 
              target="_blank" 
              rel="noopener noreferrer"
              className="text-primary hover:text-primary/80 font-medium transition-colors"
            >
              ExploitDB
            </a>
          </div>

          <div className="flex items-center justify-center gap-3 mb-10">
            <Button
              variant="outline"
              size="sm"
              asChild
              className="border-primary/30 hover:bg-primary/10 hover:border-primary/50 transition-all bg-transparent"
            >
              <a href="https://github.com/xFraylin" target="_blank" rel="noopener noreferrer">
                GitHub
              </a>
            </Button>
            <Button
              variant="outline"
              size="sm"
              asChild
              className="border-accent/30 hover:bg-accent/10 hover:border-accent/50 transition-all bg-transparent"
            >
              <a href="https://linkedin.com/in/xfraylin" target="_blank" rel="noopener noreferrer">
                LinkedIn
              </a>
            </Button>
          </div>

          <div className="max-w-2xl mx-auto">
            <div className="relative">
              <Search className="absolute left-4 top-1/2 -translate-y-1/2 h-5 w-5 text-muted-foreground" />
              <Input
                type="text"
                placeholder="example.com"
                value={domain}
                onChange={(e) => setDomain(e.target.value)}
                className="pl-12 h-14 text-center text-lg bg-card/50 backdrop-blur border-primary/30 focus:border-primary shadow-lg shadow-primary/5 rounded-xl"
              />
            </div>
            <p className="text-sm text-muted-foreground mt-3">Enter single or multiple domains separated by commas</p>
          </div>
        </div>

        <div className="space-y-6">
          {dorkCategories.map((category, categoryIndex) => (
            <div
              key={categoryIndex}
              className="bg-card/50 backdrop-blur border border-border/50 rounded-2xl p-6 shadow-lg hover:shadow-xl hover:shadow-primary/5 transition-all"
            >
              <div className="flex items-center justify-between mb-4">
                <h2 className="text-base font-bold text-foreground flex items-center gap-2">
                  <span className="h-2 w-2 rounded-full bg-primary animate-pulse" />
                  {category.title}
                </h2>
                {category.dorks.length === 1 || category.dorks.some(dork => dork.includes(' | ')) ? (
                  <Button
                    size="sm"
                    variant="outline"
                    onClick={() => openAllQueries(category.dorks)}
                    className="border-primary/40 bg-primary/5 hover:bg-primary/15 hover:border-primary text-primary font-medium gap-2 rounded-lg transition-all"
                  >
                    <ExternalLink className="h-4 w-4" />
                    Open All
                  </Button>
                ) : (
                  <div className="text-xs text-muted-foreground bg-muted/50 px-3 py-1.5 rounded-lg">
                    {category.dorks.length} queries
                  </div>
                )}
              </div>

              <div className="flex flex-wrap gap-2.5">
                {category.dorks.map((dork, dorkIndex) => {
                  const processedDork = replaceDomain(dork, domain)
                  const uniqueKey = `${categoryIndex}-${dorkIndex}`
                  const isCopied = copiedIndex === uniqueKey

                  return (
                    <div key={dorkIndex} className="group relative inline-flex items-center gap-1.5">
                      <a
                        href={generateGoogleSearchUrl(processedDork)}
                        target="_blank"
                        rel="noopener noreferrer"
                        className="inline-block px-4 py-2 bg-secondary/80 hover:bg-secondary border border-border/30 hover:border-primary/40 text-foreground rounded-lg text-sm font-mono transition-all hover:scale-105 hover:shadow-md"
                      >
                        {processedDork}
                      </a>
                      <Button
                        size="icon"
                        variant="ghost"
                        className="h-8 w-8 opacity-0 group-hover:opacity-100 transition-all hover:bg-primary/10 rounded-lg"
                        onClick={() => copyToClipboard(processedDork, uniqueKey)}
                      >
                        {isCopied ? (
                          <Check className="h-4 w-4 text-primary" />
                        ) : (
                          <Copy className="h-4 w-4 text-muted-foreground" />
                        )}
                      </Button>
                    </div>
                  )
                })}
              </div>
            </div>
          ))}
        </div>

        <div className="mt-16 text-center">
          <div className="inline-flex items-center gap-2 text-sm text-muted-foreground">
            <Sparkles className="h-4 w-4" />
            <span>Happy hunting! Remember to hack responsibly.</span>
          </div>
        </div>
      </div>
    </div>
  )
}
